import {ListGroup,ListGroupItem,Jumbotron,ProgressBar} from 'react-bootstrap';
import React, { Component } from 'react';
import './index.scss';

class NavList extends Component {
  constructor(props){
    super(props);
    this.last=null;
    this.frame=null;
    this.state={
      link:null
    }
  }
  load=(event,link)=>{
    let {target}=event;
    this.last=link;

    this.setState({
      link
    });
  }
  render() {
    let data=this.props.data;
    let link=data.link;
    return (
      <div className="frame">
        {
          data.link
          ?(
            <React.Fragment>
            <div className="loading" style={{display:this.last===link?'none':'block'}}>loading...<br/><ProgressBar active now={100} /></div>
            <iframe src={data.link}  frameBorder="0" allowFullScreen="true" onLoad={(event)=>{this.load(event,data.link)}} style={{display:this.last===link?'block':'none'}}></iframe>
            </React.Fragment>
          )
          :(<Jumbotron style={{}}>
            <h1>multiple links</h1>
            <p>
              <code>
                {`json example:`}<br/><br/>
                {`[`}<br/>
                {`{name:'search group1'},`}<br/>
                {`{name:'google',link:'http://www.google.com',des:'search1'},`}<br/>
                {`{name:'baidu',link:'http://www.baidu.com',des:'search2'},`}<br/>
                {`{name:'search group2'},`}<br/>
                {`{name:'mdn',link:'http://www.mdn.com',des:'search3'}`}<br/>
                {`]`}
              </code>
            </p>
          </Jumbotron>)
        }

      </div>
    );
  }
}

export default NavList;



