import {Nav,Navbar,NavItem} from 'react-bootstrap';
import React, { Component } from 'react';
import './index.css';


class Header extends Component{
  render(){

    return (
      <Navbar>
        <Navbar.Header>
          <Navbar.Brand>
            <a href="#home">wct webq</a>
          </Navbar.Brand>
        </Navbar.Header>
        <Nav className="pull-right">
          {
            this.props.data.link&&(
              <NavItem href={this.props.data.link} target="_blank">
                {this.props.data.name} link button(if iframe can not show)
              </NavItem>
            )
          }
        </Nav>
      </Navbar>

    )

  }
}


export default Header;



