import React, { Component } from 'react';
import logo from './logo.svg';
import './App.scss';
import NavList from './components/NavList/index.js';
import Header from './components/Header/index.js';
import Frame from './components/Frame/index.js';

let g=window.webqConfig?window.webqConfig:[
  {name:'you need write a json config in webq',color:'white'}
];

class App extends Component {
  constructor(props){
    super(props);
    this.list=g?g:[];
    this.state={
      focus:g[0]?g[0]:{}
    }
  }
  changeIframe=(index)=>{
    let focusData=this.list[index];
    if(focusData.link){
      this.setState({
        focus:this.list[index]
      });
    }
  }
  render() {
    return (
      <div className="App">
        <Header data={this.state.focus} />
        <div className="layout">
          <div className="layout-center">
            <Frame data={this.state.focus}/>
          </div>
          <div className="layout-bottom" ><NavList list={this.list} focusTrigger={this.changeIframe}/></div>
        </div>
      </div>
    );
  }
}

export default App;



