import {Nav,NavItem,Label} from 'react-bootstrap';
import React, { Component } from 'react';
import './index.css';
import classnames from 'classnames';

class NavList extends Component {
  constructor(props){
    super(props);
    this.focus=0;
    this.state={
      focus:0
    }
  }
  setFocus=(focus)=>{
    this.setState({
      focus
    });
    let {focusTrigger}=this.props;
    if(typeof focusTrigger==='function'){
      focusTrigger(focus);
    }
  }
  render() {

    return (
      <div className="nav-list">
        <Nav >

           {
             this.props.list.map((obj,ind)=>{
               return (
                 obj.link
                 ?(
                     <NavItem target="_blank" key={ind} style={{display:'inline-block'}} key={ind} title={obj.name} className={classnames({'nav-item':ind===this.state.focus})}  onClick={()=>{this.setFocus(ind)}}    >
                        <span style={obj.color?{color:obj.color}:{}}>
                          {obj.name}{obj.des&&("("+obj.des+")")}
                        </span>
                     </NavItem>
                   )
                 :(
                     <React.Fragment>
                     <br/><Label bsStyle="primary" key={ind} style={obj.color?{color:obj.color,marginRight:'5px',marginLeft:'5px'}:{marginRight:'5px',marginLeft:'5px'}}>{obj.name}{obj.des&&("("+obj.des+")")}</Label>
                     </React.Fragment>
                   )


               )
             })
           }
        </Nav>
      </div>
    );
  }
}

export default NavList;



